import { useEffect, useState } from 'react'
import {useDispatch, useSelector} from "react-redux"
import Select from 'react-select'
import { selectThemeColors } from '@utils'
import { useForm } from 'react-hook-form'
import classnames from 'classnames'
import { DragDrop } from '@uppy/react'
import Uppy from '@uppy/core'
import Table from '../../Table'
import '@uppy/core/dist/style.css'
import BreadCrumbs from '@components/breadcrumbs'
import thumbnailGenerator from '@uppy/thumbnail-generator'
import "./styles.scss"
import './styles copy.scss'
import '@styles/react/libs/file-uploader/file-uploader.scss'
import { Card, CardHeader, CardTitle, CardBody, Row, Col, Label, Button} from 'reactstrap'
import {getcricketTotal, getAllSeries, getAllFixture, collectseriesid, collectfixtureid, addfile} from "../../store/actions"
// import Table from  "./list/Table"


// ** Import Scrollbar Paginate Component
import { AsyncPaginate } from "react-select-async-paginate"

const SelectReact = () => {

  const defaultValues = {
    reactSelect: null
  }
  const { handleSubmit } = useForm({ defaultValues })

    const store = useSelector((state) => state.scorecard)
    const store1 = useSelector((state) => state.teams)
    const [sid, setSid] = useState(0)
    const [val, setVal] = useState(false)
    const [fid, setFid] = useState(0)
    const [error, setError] = useState(false)
    const [data, setData] = useState(null)
    const [refresh, setRefresh] = useState(false)
    const [img, setImg] = useState(null)
    const [table, setTable] = useState(false)
    const [imageError] = useState(false)
    const [serilist, setSerilist] = useState("")
    const [fixlist, setFixlist] = useState("")
    const [page, setPage] = useState(1)
    const [value] = useState(null)
    const dispatch = useDispatch()

    const uppy = new Uppy({
      meta: { type: 'avatar' },
      restrictions: { maxNumberOfFiles: 1 },
      autoProceed: true
    })


    useEffect(() => {
      dispatch(getcricketTotal(page))
    }, [page])
  
    useEffect(() => {     
      dispatch(getAllFixture(serilist))
      console.log(serilist)
      console.log(store.fixture_name)
      console.log(store)
      console.log(sid)
    }, [serilist])

    uppy.use(thumbnailGenerator)

    uppy.on('file-added', (file, preview) => {
      console.log(file.data.lastModified)
      console.log(file.data)
      console.log(uppy)
      console.log(preview)
      setImg(preview)
      setData(file.data)
    })

   console.log(store.series_name)
    const fixture = store.fixture_name.map(function (fix) {
      return { value: fix.id, label: fix.fixture_name }
    })  

    // const fixture = store.fixture_name.map(function (fix) {
    //   return { value: fix.fixture_name, label: fix.fixture_name }
    // })

    const filterid = store.series_name.filter(s => s.series_name === serilist)
    const filterid2 = store.fixture_name.filter(f => f.fixture_name === fixlist)
    console.log(store.fixture_name)

    const submit = (e) => {
      e.preventDefault()
      setVal(true)
      if (serilist === "") {
        setError(true)
        setTable(false)
      } else {
        const param = new FormData()
        param.append('series_id', serilist)
        param.append('fixture_id', fixlist)
        param.append('file', data)
        dispatch(addfile(param))
        // dispatch(getplayerreport(filterid[0]?.id, filterid2[0]?.id))
        dispatch(collectseriesid(filterid[0]?.id))
        dispatch(collectfixtureid(filterid2[0]?.id))
        console.log(filterid[0]?.id, filterid2[0]?.id)   
        setTable(true)     
      }
    }
    
    console.log(store.player_reports)
    // onChange={(e) => setPrizeform({...prizeform, category: e.value})}

    const serieslist = (e) => {
      setVal(false)
      setError(false)
      console.log(error)
      setSerilist(e?.value)
      setRefresh(true)
      console.log('change')
      // dispatch(getAllFixture(filterid[0]?.id))
      setSid(filterid[0]?.id)
      // setChange(true)
      console.log(filterid[0]?.id)
      // setClick(false)
      // return store.series_name.filter(s => s.series_name === list)
    }

    const fixturelist = (e) => {
      // setClick(true)
      setFixlist(e?.value)
      setRefresh(false)
      setFid(filterid2[0]?.id)
      console.log(filterid2[0]?.id)
      console.log(fid)
      // return store.series_name.filter(s => s.series_name === list)
    }

    console.log(filterid[0]?.id)
    console.log(filterid2[0]?.id)
      // setId(list.filter(l => l === store.series_name.series_name))    }

    useEffect(() => {
       dispatch(getAllSeries())
        console.log(store)
      }, [])
      
      useEffect(() => {
      dispatch(getAllFixture(filterid[0]?.id))
      console.log("now")
      console.log(store.fixture_name)
      }, [sid])

    console.log(store.fixture_name)

    const handleKeyPress = (e) => {
      if (e.keyCode === 13) {
        dispatch(collectseriesid(filterid[0]?.id))        
      }
    }
  
         // ************************************************************************************* //
  
     const options = []
     for (let i = 0; i < store1.cricket_total?.data?.total; i++) {
       if (page === store1.cricket_total?.data?.current_page) {
         options.push({
           value: `${store1.cricket_total?.data?.data[i]?.id}`,
           label: `${store1.cricket_total?.data?.data[i]?.series_name}`
         })
       }
       
     }
     const sleep = ms => new Promise(resolve => { setTimeout(() => { resolve() }, ms) })
     const optionsPerPage = 10
     
     const loadOptions = async (search, prevOptions, page) => {
       await sleep(1000)
       if (page === store1.cricket_total?.data?.current_page) {
         const nextData = store1.cricket_total?.data?.data?.map((nextData) => { return { value: nextData.id, label: nextData.series_name } })
         const scrollLength = [...prevOptions, ...nextData]
         let filteredOptions
         if (!search) {
           filteredOptions = scrollLength.length > 10 ? scrollLength : options
         } else {
           const searchLower = search.toLowerCase()
           filteredOptions = [...scrollLength.length > 10 ? scrollLength : options].filter(({ label }) => label.toLowerCase().includes(searchLower)
           )
         }
     
         const hasMore = Math.ceil(store1.cricket_total?.data?.total / optionsPerPage) > page
         const slicedOptions = filteredOptions.slice(
           (page - 1) * optionsPerPage,
           page * optionsPerPage
         )
     
         return {
           options: slicedOptions,
           hasMore
         }
       }
     }
     const defaultAdditional = {
       page: 1
     }
     
     const loadPageOptions = async (q, prevOptions, { page }) => {
       setPage(page)
       const { options, hasMore } = await loadOptions(q, prevOptions, page)
       return {
         options:[...options],
         hasMore,
     
         additional: {
           page: page + 1
         }
       }
     }
     
      // ************************************************************************************* //

  return (
    <>
    <BreadCrumbs breadCrumbTitle='Corporate Scorecard' breadCrumbActive='scorecard' />
    <Card>
      <CardHeader>
        <CardTitle tag='h4'>{store.isloading }</CardTitle> 
      </CardHeader>
      <CardBody>
        <p>
          <a className='my-50' target='_blank' href='https://react-select.com/home' rel='noopener noreferrer'>
           
          </a>
        </p>
        <Row>
        <Col className='mb-1' md='4' sm='12'>
          <Label>series</Label>
              <AsyncPaginate
              menuPortalTarget={document.querySelector('body')}
                theme={selectThemeColors}
                className={classnames('react-select', { 'is-invalid': serilist === "" && val })}
                classNamePrefix='select'
                additional={defaultAdditional}
                value={value?.label}
                loadOptions={loadPageOptions}
                onChange={(e) => serieslist(e)}
                onKeyDown={(e) => handleKeyPress(e)}
              />
          {serilist === "" && val ?  <h5 className='text-danger'>Please enter a value</h5> : ""}
          </Col>
          <Col className='mb-1' md='6' sm='12'>
            <Label>fixture</Label>
            <Select
              onChange={(e) => fixturelist(e)}
              theme={selectThemeColors}
              className='react-select'
              value={refresh ? " " : fid} 
              classNamePrefix='select'
              options={fixture ? fixture  : ""}
              isClearable={true}
            />
          </Col>
        </Row>
        <Row className="wrapper-add" >
        <Col className="add-post-edit">
         <div className={`mt-4 ${imageError ? 'img-error-post' : ''}`}>
         <DragDrop uppy={uppy}
           locale={{
            strings: {
              // Text to show on the droppable area.
              // `%{browse}` is replaced with a link that opens the system file selection dialog.
              dropHereOr: 'Drop file or %{browse}',
              // Used as the label for the link that opens the system file selection dialog.
              browse: 'browse'
            }
          }}
         />
        {img !== null ? <img className='rounded mt-2' src={img} alt='avatar' /> : null}
                {img && (
                  <div className="uploaded-files">
                    <img src={img} className="post-preview" />
                  </div>
                )}
              </div>
              </Col>
             </Row>
             <div className="mt-3">
        <Button.Ripple 
        color='primary'
        onClick={(e) => handleSubmit(submit(e))}
        >submit</Button.Ripple>
        </div>                  
      </CardBody>
      {table ? <Table/> : ''}
    </Card>
    </>
  )
}
export default SelectReact